*,
*:after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
  height: 100vh;
}

body {
  font-family: "Spartan", sans-serif;
  font-weight: 500;
  font-size: 1.2rem;
  line-height: 1.5;
  height: 100vh;
}

.container {
  display: grid;
  grid-template-columns: 29vw 3fr 1fr 32vw;
  grid-template-rows: 7rem 55vh 1fr;
}

.heading--primary {
  font-size: 3.4rem;
  line-height: 1;
  padding: 0;
}
.heading--secondary {
  color: #000000;
  text-transform: uppercase;
  letter-spacing: 5px;
  font-size: 1.4rem;
}

.paragraph {
  color: #a1a1a1;
}

.btn {
  background-color: transparent;
  border: none;
  text-transform: uppercase;
  letter-spacing: 8px;
  cursor: pointer;
  transition: all 0.3s;
}
.btn:hover {
  color: #a1a1a1;
}
.btn__icon {
  transition: all 0.3s;
}

.btn:hover .btn__icon {
  filter: opacity(35%);
}

.carousel {
  grid-column: 1/3;
  grid-row: 1/3;
  position: relative;
}
.carousel::before {
  content: "";
  background: radial-gradient(transparent, rgba(0, 0, 0, 0.3));
  width: 100%;
  height: 100%;
  z-index: 20;
  position: absolute;
}
.carousel__main {
  height: 100%;
  width: 100%;
  overflow: hidden;
  display: flex;
  position: relative;
}
.carousel__btns {
  position: absolute;
  right: 0;
  bottom: 0;
  height: 8rem;
  width: 9.8vw;
  transform: translateX(100%);
  display: flex;
}
.carousel__btn {
  background-color: #000000;
  border: none;
  padding: 2rem;
  cursor: pointer;
  flex: 1;
}
.image {
  overflow: hidden;
}
.image--black {
  grid-column: 1/2;
}
.image--light {
  grid-column: -2/-1;
}
.image__photo {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.logo {
  height: 1.3rem;
  margin-left: 6rem;
}

.slide {
  position: absolute;
  width: 100%;
  height: 100%;
  transition: all 0.3s;
}
.slide__photo {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.header {
  grid-column: 1/2;
  grid-row: 1/2;
  z-index: 200;
  display: flex;
  justify-content: space-between;
  align-items: end;
  gap: 2rem;
}

.navigation__list {
  display: flex;
  list-style: none;
  gap: 3rem;
  height: 100%;
}
.navigation__item {
  position: relative;
}
.navigation__item::after {
  content: "";
  height: 1.4px;
  background-color: transparent;
  width: 40%;
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  transition: all 0.3s;
}
.navigation__item:hover::after {
  background-color: #ffffff;
}
.navigation__link {
  color: #ffffff;
  text-decoration: none;
}
.navigation__toggle {
  display: none;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

.section {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.section__discover {
  grid-column: -3/-1;
  grid-row: 1/3;
  padding: 0rem 8rem;
  align-items: start;
  gap: 2rem;
}
.section__about {
  grid-row: -2/-1;
  grid-column: 2/4;
  padding: 0rem 6rem;
  gap: 1rem;
}